<template>
  <v-app>
    <v-container>
      <h1
        class="display-4 font-weight-light text-center header blue--text text--darken-3"
      >COVID-19 Tracker</h1>
      <v-divider class="divider"></v-divider>

      <v-layout class="tabs" row wrap justify-space-around>
        <v-flex xs12 md3 lg3>
          <div>
            <p class="headline text-center">Total Cases Confirmed:</p>
            <v-chip
              class="chips headline text-center light-blue darken-1 white--text"
            >{{ totalConfirmed.toLocaleString('en') }}</v-chip>
          </div>
        </v-flex>
        <v-flex xs12 md3 lg3>
          <div>
            <p class="headline text-center">Total Deaths:</p>
            <v-chip
              class="chips headline text-center red darken-1 white--text"
            >{{ totalDeaths.toLocaleString('en') }}</v-chip>
          </div>
        </v-flex>
        <v-flex xs12 md3 lg3>
          <div>
            <p class="headline text-center">Total Recoveries:</p>
            <v-chip
              class="chips headline text-center green darken-1 white--text"
            >{{ totalRecovered.toLocaleString('en') }}</v-chip>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: "HomePage",
  components: {},
  data: () => ({
    totalCases: "",
    totalConfirmed: "",
    totalDeaths: "",
    totalRecovered: ""
  }),
  created() {
    this.$http
      .get("https://coronavirus-19-api.herokuapp.com/all")
      .then(function(data) {
        this.totalCases = data.body;

        this.totalConfirmed = this.totalCases.cases;
        this.totalDeaths = this.totalCases.deaths;
        this.totalRecovered = this.totalCases.recovered;
      });
  },
  methods: {
    formatNumber: function() {}
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
body {
  font-family: "Montserrat";
}

.header {
  margin-top: 100px;
}

.tabs {
  margin-top: 30px;
}

.chips {
  margin-left: 90px;
}

.divider {
  margin-top: 50px;
}
</style>
