<template>
  <v-app>
    <v-container>
      <h1
        class="display-4 font-weight-light text-center header blue--text text--darken-3"
      >Continents</h1>
      <v-divider class="divider"></v-divider>
      <v-row>
        <v-col
          cols="12"
          xs="12"
          sm="12"
          md="6"
          lg="4"
          v-for="continent in continents"
          :key="continent.continent"
        >
          <v-card color="white" class="black--text ml-3" dark>
            <v-card-title>
              <span class="title font-weight-light">{{ continent.continent }}</span>
            </v-card-title>
            <v-card-actions>
              <div>
                <span class="ma-3 card-action">Cases:</span>
                <v-chip
                  class="ma-2"
                  color="blue darken-1"
                  text-color="white"
                >{{ continent.cases.toLocaleString('en') }}</v-chip>
              </div>

              <div>
                <span class="ma-3 card-action">Deaths:</span>
                <v-chip
                  class="ma-2"
                  color="red darken-1"
                  text-color="white"
                >{{ continent.deaths.toLocaleString('en') }}</v-chip>
              </div>

              <div>
                <span class="ma-3 card-action">Recoveries:</span>
                <v-chip
                  class="ma-2"
                  color="green darken-1"
                  text-color="white"
                >{{ continent.recovered.toLocaleString('en') }}</v-chip>
              </div>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: "Continents",
  data: () => ({
    continents: []
  }),
  created() {
    this.$http
      .get("https://disease.sh/v2/continents?yesterday=false")
      .then(function(data) {
        console.log(data);
        this.continents = data.body;
      });
  }
};
</script>

<style scoped>
.header {
  margin-top: 50px;
}

.tabs {
  margin-top: 20px;
}

