<template>
  <v-app>
    <v-container>
      <h1
        class="display-4 font-weight-light text-center header blue--text text--darken-3"
      >Global Cases</h1>
      <v-divider class="divider"></v-divider>

      <v-row class="mt-5">
        <v-col cols="12" xs="12" sm="12" md="6" lg="8" class="mx-auto">
          <v-card color="white" class="black--text" dark>
            <v-card-title>
              <h1 class="display-1 ma-5 font-weight-light text-lg-center">Cases:</h1>
              <v-chip
                class="pa-5 ma-2 display-1 ma-5 font-weight-light text-lg-center"
                color="blue darken-1"
                text-color="white"
              >{{ totalConfirmed.toLocaleString('en') }}</v-chip>
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
      <v-row class="mt-5">
        <v-col cols="12" xs="12" sm="12" md="6" lg="8" class="mx-auto">
          <v-card color="white" class="black--text mx-lg-auto" dark>
            <v-card-title>
              <h1 class="display-1 ma-5 font-weight-light">Deaths:</h1>
              <v-chip
                class="pa-5 ma-2 display-1 ma-5 font-weight-light"
                color="red darken-1"
                text-color="white"
              >{{ totalDeaths.toLocaleString('en') }}</v-chip>
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
      <v-row class="mt-5">
        <v-col cols="12" xs="12" sm="12" md="6" lg="8" class="mx-auto">
          <v-card color="white" class="black--text mx-lg-auto" dark>
            <v-card-title>
              <h1 class="display-1 ma-5 font-weight-light">Recovered:</h1>
              <v-chip
                class="pa-5 ma-2 display-1 ma-5 font-weight-light"
                color="green darken-1"
                text-color="white"
              >{{ totalRecovered.toLocaleString('en') }}</v-chip>
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: "HomePage",
  components: {},
  data: () => ({
    totalCases: [],
    totalConfirmed: "",
    totalDeaths: "",
    totalRecovered: ""
  }),
  created() {
    this.$http
      .get("https://coronavirus-19-api.herokuapp.com/all")
      .then(function(data) {
        this.totalCases = data.body;

        this.totalConfirmed = this.totalCases.cases;
        this.totalDeaths = this.totalCases.deaths;
        this.totalRecovered = this.totalCases.recovered;
      });
  },
  methods: {
    formatNumber: function() {}
  }
};
</script>

<style scoped>
.header {
  margin-top: 50px;
}

.divider {
  margin-top: 50px;
}
</style>
