<template>
  <v-app>
    <v-container>
      <h1 class="display-4 font-weight-light text-center header blue--text text--darken-3">Countries</h1>
      <v-divider class="divider"></v-divider>

      <v-row>
        <v-col cols="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="ml-5"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-data-table
            :headers="headers"
            :items="country"
            :search="search"
            :items-per-page="5"
            class="elevation-1 dataTable"
          ></v-data-table>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: "Countries",
  data: () => ({
    search: "",
    country: [],
    headers: [
      { text: "Country Name", value: "country" },
      { text: "New Cases", value: "todayCases" },
      { text: "Total Cases", value: "cases" },
      { text: "Total Deaths", value: "deaths" },
      { text: "New Deaths", value: "todayDeaths" },
      { text: "Total Recoveries", value: "recovered" },
      { text: "Active Cases", value: "active" },
      { text: "Critical Cases", value: "critical" },
      { text: "Total Tests", value: "totalTests" }
    ]
  }),
  created() {
    this.$http
      .get("https://coronavirus-19-api.herokuapp.com/countries")
      .then(function(data) {
        // console.log(data);
        this.country = data.body.slice(1, 214);
      });
  },
  methods: {}
};
</script>

<style scoped>
.dataTable {
  margin-top: 30px;
}

.header {
  margin-top: 50px;
}

.divider {
  margin-top: 50px;
}
</style>
